.app_container{
    display: flex;
    justify-content: flex-start;
    flex-flow: column nowrap;
    width: 100vw;
    height: 100vh;
    padding: 10px;
    overflow: hidden;
    background: #ECF9FF;
}
.app_title{
    width: 100%;
    border-bottom: 1px solid rgba(54, 69, 79, 0.34);
    font-size: 2rem;
    font-weight: 700;
    margin: 0.5rem 0;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
}
.gallery_view{
    display: grid;
    gap: 0.75rem;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(3,1fr);
    width: 100%;
    height: 90%;
    background: #ECF9FF;
    overflow: hidden;
}

